.header_container {
  height: 106px;
  width: 100%;
}

.header_wrapper {
  padding-top: 20px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
@media screen and (max-width: 800px) {
  .header_wrapper {
    padding: 20px 10px 0;
    position: fixed;
    background-color: #FFF8F0;
    opacity: 0.9;
    z-index: 10;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 500ms;
  }
  .header_wrapper.hide {
    top: -106px;
  }
}

@media screen and (max-width: 800px) {
