}
.nav_wrapper.start ul.start .header_nav_separator {
  background-color: #FFFFFF;
}
@media screen and (max-width: 800px) {
  .nav_wrapper.start ul.start {
    width: 100%;
  }
}

@media screen and (max-width: 800px) {
  .nav_wrapper {
    display: none;
  }
}
.nav_wrapper ul {
  width: 360px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-self: center;
}
.nav_wrapper ul.start {
  width: 404px;
}
.nav_wrapper ul.start .header_nav_separator {
  background-color: #FFFFFF;
}
@media screen and (max-width: 800px) {
  .nav_wrapper ul.start {
    width: 100%;
  }
}
.nav_wrapper ul.start .header_nav_link {
  font-weight: 200;
  font-size: 24px;
  line-height: 28px;
  color: #FFFFFF;
}
@media screen and (min-width: 800px) {
  .nav_wrapper ul.start .header_nav_link.active:after {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    bottom: -3px;
    width: 100%;
    height: 1px;
    background-color: #FFFFFF;
    transition: 0.7s;
  }
  .nav_wrapper ul.start .header_nav_link:after {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    bottom: -3px;
    width: 0;
    height: 1px;
    background-color: #FFFFFF;
    transition: 0.7s;
  }
}
@media screen and (max-width: 800px) {
  .nav_wrapper ul {
    width: 100%;
    height: 55vh;
    flex-direction: column;
  }
}
.nav_wrapper ul .header_nav_link {
  font-family: "Raleway", sans-serif;
  font-size: 16px;
  font-weight: 300;
  color: #000;
  position: relative;
  text-transform: lowercase;
}
@media screen and (min-width: 800px) {
  .nav_wrapper ul .header_nav_link.active:after {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    bottom: -3px;
    width: 100%;
    height: 2px;
    background-color: #000;
    transition: 0.7s;
  }
  .nav_wrapper ul .header_nav_link:after {
    content: "";
    display: block;
    position: absolute;
    right: 0;
    bottom: -3px;
    width: 0;
    height: 2px;
    background-color: #000;
    transition: 0.7s;
  }
  .nav_wrapper ul .header_nav_link:hover:after {
    content: "";
    width: 100%;
    display: block;
    position: absolute;
    left: 0;
    bottom: -3px;
    height: 2px;
    background-color: #E0BE9C;
    transition: 0.7s;
  }
}
@media screen and (max-width: 800px) {
  .nav_wrapper ul .header_nav_link {
    font-size: 28px;
    font-weight: 400;
    color: #FFF;
    letter-spacing: 3px;
  }
}
.nav_wrapper .header_nav_separator {
  width: 3px;
  height: 3px;
  border-radius: 50%;
  background-color: #000;
}
.nav_wrapper .header_nav_separator:last-child {
  display: none;
}
@media screen and (max-width: 800px) {
  .nav_wrapper .header_nav_separator {
    width: 40px;
    height: 1px;
    opacity: 0.5;
    background-color: #E5E0E0;
    border-radius: 4px;
  }
}

